import React from 'react';

import Icon from '../../common/Icon';
import Button from '../../common/Button';
import Modal from '../Modal'

import { buildQuery } from '../../../const/urlHelper'

import './AddAccountModal.scss'

const AddAccountModal = ({ basic, onOpenModal, onCloseModal, onSetCurrentElem }) => {

  const runOAuth = (event) => {
    const { vkAuthAppId, vkAuthCallback } = basic

    const vkAuthUrl = 'https://oauth.vk.com/authorize?'
      + buildQuery({
        client_id: vkAuthAppId,
        redirect_uri: `${vkAuthCallback}?mode=pagesList`,
        display: 'page',
        response_type: 'token',
        v: '5.74',
        scope: 'groups',
      });

    const width = 800;
    const height = 400;
    const centerPosition = { top: 0, left: 0 }
    //Ð¾ÑÐºÑÑÐ²Ð°ÐµÐ¼ Ð¾ÐºÐ½Ð¾ Ð² popup'Ðµ
    const popupWindow = window.open(vkAuthUrl, 'VK Auth',
      'height=' + height + ',width=' + width + (centerPosition !== null
        ? ',left=' + centerPosition.left + ',top=' + centerPosition.top
        : ''));

    setTimeout(function () {
      popupWindow.postMessage('wh-vk-oauth-result', '*')
    }, 2000);

    window.addEventListener('message', function (e) {
      if (e.data && e.data.source !== '@devtools-page') {
        popupWindow.close()
        try {
          const eventData = JSON.parse(e.data);
          if (eventData['data'].hasOwnProperty('groups')) {
            onSetCurrentElem({ vkGroups: eventData['data']['groups'] })
            onOpenModal('addVkontakteAccount')
          }
        } catch (err) {
          console.log(err, e);
        }
      }
    })
  };

  const showAccountsList = () => {
    const accounts = [
      // { label: 'facebook', value: 'Facebook', modal: 'AddFacebookAccount' },
      { label: 'Telegram', value: 'telegram', modal: 'addTelegramAccount' },
      { label: 'Viber', value: 'viber', modal: 'addViberAccount' },
      { label: 'ÐÐºÐ¾Ð½ÑÐ°ÐºÑÐµ', value: 'vkontakte', modal: 'addVkontakteAccount' }
    ]

    return (
      <div className="account-list">
        {accounts.map((item, i) => {
          return (
            <div className='account' key={i}>
              <div className='account-type'>
                <Icon
                  name={item.value}
                  width={30}
                  className='account-icon'
                />
                <span>{item.label}</span>
              </div>
              <Button
                onClick={() => {
                  if (item.value === 'vkontakte') {
                    runOAuth()
                    onCloseModal()
                  } else {
                    onOpenModal(item.modal)
                  }
                }}
              >
                ÐÐ¾Ð´ÐºÐ»ÑÑÐ¸ÑÑ
            </Button>
            </div>
          )
        })}
      </div>
    )
  }

  return (
    <Modal
      title='ÐÐ¾Ð±Ð°Ð²Ð¸ÑÑ Ð°ÐºÐºÐ°ÑÐ½Ñ'
      onClose={onCloseModal}
    >
      <div className="modal-content">
        ÐÐ»Ñ Ð½Ð°ÑÐ°Ð»Ð° Ð½ÐµÐ¾Ð±ÑÐ¾Ð´Ð¸Ð¼Ð¾ Ð´Ð¾Ð±Ð°Ð²Ð¸ÑÑ ÑÑÑÐµÑÑÐ²ÑÑÑÐ¸Ð¹ Ð°ÐºÐºÐ°ÑÐ½Ñ Ð¾Ð´Ð½Ð¾Ð³Ð¾ Ð¸Ð· Ð¼ÐµÑÑÐµÐ½Ð´Ð¶ÐµÑÐ¾Ð², Ðº ÐºÐ¾ÑÐ¾ÑÐ¾Ð¼Ñ Ð² Ð´Ð°Ð»ÑÐ½ÐµÐ¹ÑÐµÐ¼ Ð¼Ð¾Ð¶Ð½Ð¾ Ð±ÑÐ´ÐµÑ Ð¿ÑÐ¸Ð²ÑÐ·Ð°ÑÑ Ð½ÐµÐ¾Ð³ÑÐ°Ð½Ð¸ÑÐµÐ½Ð½Ð¾Ðµ ÐºÐ¾Ð»Ð¸ÑÐµÑÑÐ²Ð¾ ÑÐ°Ñ-Ð±Ð¾ÑÐ¾Ð² Ð¸Ð»Ð¸ Ð°Ð²ÑÐ¾Ð²Ð¾ÑÐ¾Ð½Ð¾Ðº.

      {showAccountsList()}
      </div>
    </Modal>
  );
};

export default AddAccountModal;