/*
 * param  iNumber Integer Ð§Ð¸ÑÐ»Ð¾ Ð½Ð° Ð¾ÑÐ½Ð¾Ð²Ðµ ÐºÐ¾ÑÐ¾ÑÐ¾Ð³Ð¾ Ð½ÑÐ¶Ð½Ð¾ ÑÑÐ¾ÑÐ¼Ð¸ÑÐ¾Ð²Ð°ÑÑ Ð¾ÐºÐ¾Ð½ÑÐ°Ð½Ð¸Ðµ
 * param  aEndings Array ÐÐ°ÑÑÐ¸Ð² ÑÐ»Ð¾Ð² Ð¸Ð»Ð¸ Ð¾ÐºÐ¾Ð½ÑÐ°Ð½Ð¸Ð¹ Ð´Ð»Ñ ÑÐ¸ÑÐµÐ» (1, 4, 5),
 *         Ð½Ð°Ð¿ÑÐ¸Ð¼ÐµÑ ['ÑÐ±Ð»Ð¾ÐºÐ¾', 'ÑÐ±Ð»Ð¾ÐºÐ°', 'ÑÐ±Ð»Ð¾Ðº']
 * return String
 */
const getNumEnding = (iNumber, aEndings) => {
  var sEnding, i;
  iNumber = iNumber % 100;
  if (iNumber >= 11 && iNumber <= 19) {
    sEnding = aEndings[2];
  }
  else {
    i = iNumber % 10;
    switch (i) {
      case (1): sEnding = aEndings[0]; break;
      case (2):
      case (3):
      case (4): sEnding = aEndings[1]; break;
      default: sEnding = aEndings[2];
    }
  }
  return sEnding;
}

export default getNumEnding